.contenedor__texto {
    display: flex;
    justify-content: center;
    align-content: center;
    margin: 0 auto 15rem auto;
    
    @include tablet {
        margin: 5rem auto 15rem auto;
    }     
}
.texto {    
    display: inline-block;
    text-align: center;
    padding: 1rem;

    & h1,
    h2 {
        color: $blanco;
        font-weight: 300;
        margin: 0;
    }
    &__nombre {
        margin-bottom: 1rem;
        font-size: 3.5rem;
        line-height: 5rem;
        display: flex;
        overflow: hidden;
        @include tablet {
            font-size: 5rem;
        }
        & >div {
            display: flex;
            flex-direction: column;
            position: relative;
            transition-property: transform;
            transition-duration: .3s;
            &.animacion {
                transform: translateY(-10rem);
            }
            & span {
                font-weight: 400;
            }
            & .segunda-linea {
                position: absolute;
                top: 10rem;
            }
        }
    }
    &__profesion {
        font-size: 1.5rem;
        background-color: $grisOscuro;
        border-radius: .3rem;
        padding: .3rem 2rem;
        transition-property: background-color box-shadow transform ;
        transition-duration: .3s;
        text-transform: uppercase; 
        @include tablet {
            font-size: 2.3rem;
        }  
        &:hover {
            background-color: $primario;
            box-shadow: 0px 10px 15px 0px rgba(0,0,0,0.75); 
            transform: scale(1.01); 
        }            
    }
   
}