.portafolios {
    background-color: $gris;
    padding: 5rem 0;    
    text-align: center;    
    &__titulo {       
        @include headingBorder( $secundario, $blanco );
        margin: 4rem auto 6rem auto;    
    }
    &__grid { 
        @include grid(1, 3rem);
               
        @include tablet {             
            @include grid(2, 3rem);
        }
        @include desktop { 
            @include grid(3, 3rem);
        }
    } 
   
}
.portafolio {
    border: 1px solid darken( $gris, 10% );
    border-radius: .3rem;
    background-color: $blanco;
    overflow: hidden;       
    max-width: 100%; 
    transition-property: box-shadow transform;
    transition-duration: .3s;
    
    &:hover {
        box-shadow: 0px 10px 15px 0px rgba(0,0,0,0.75); 
        transform: scale(1.01);
        
    }
    &__meta {
        background-color: $primario;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        padding: .4rem 0;
        color: $blanco;        
    }
    &__skills {
        font-size: 1.6rem;        
    }
    &__nombre {
        font-size: 2.6rem;
        color: $secundario;
        margin: 0;
    }
    &__contenido {
        padding: 1rem;
    }
    &__descripcion {
        color: $grisOscuro;
        font-size: 1.6rem;
    }
    &__icono-github {
        margin: 0 auto;
    }
    &__enlace {
        @include boton($primario, $blanco);
        display: block;
        font-size: 1.6rem;
        padding: 1rem;
    }
    &__btn {
        margin: 5rem auto 2rem auto;
        & a {
            color: $blanco;
        }
     
    }
}