.cursor-not-allowed {
    cursor: not-allowed;
}
.opacity-50 {
    opacity: .5;
}
.error-msj {
    border: .3rem solid $error !important;
    border-radius: .3rem;
    padding: 1.5rem 2rem;
}
.ok-msj {
    border: .3rem solid $ok-msj !important;
    border-radius: .3rem;
    padding: 1.5rem 2rem;
}
.error-bg {
    border: .3rem solid $error;
    background-color: $errorBg;
    padding: 1.5rem 2rem;
    color: $error;
    border-radius: .3rem;
    width: 100%;
    text-align: center;
    font-weight: 700;
    font-size: 1.7rem;
}
.msj {
    text-align: center;
    padding: 1.5rem 2rem;
    background-color: $ok-msj;
    color: $blanco;
    text-transform: uppercase;
    font-weight: 700;
    border-radius: .3rem;
    width: 100%;
    font-size: 1.7rem;
}