.copyright { 
    border-bottom: .5rem solid $secundario;    
    padding: 1rem 0;
    background-color: $primario;
   
    &__grid {      
   
        @include tablet {
            display: flex;
            justify-content: space-between;
        }
    }   

    &__nav {      
        display: flex;            
        flex-direction: column;
        align-items: center;
        margin: 0;    
      
        @include tablet {
            flex-direction: row;
            justify-content: flex-end;         
        }
        
    } 
    &__enlace {            
        margin: 0 2rem;
        color: $blanco;
        padding-bottom: .7rem;
        display: inline-block;            
        border-bottom: .2rem solid transparent;
        font-size: 1.8rem;
        transition-property: color transform;
        transition-duration: .3s;
        font-weight: 300;  
                  
        &:hover{                                 
            color: $gris;
            border-bottom: .2rem solid $gris;
            transform: translateY(.1rem);                         
        }             
    } 
    &__texto {
        font-size: 2rem;
        margin-top: 3rem;
        text-align: center;
        color: $blanco;
        @include tablet {
            margin: 0
        }
    }
}