

.webp .header {
    background-image: url('../img/bg.webp');
}
.avif .header {
    background-image: url('../img/bg.avif');
}
.notavif.notwebp .header {
    background-image: url('../img/bg.jpg') $gris;
}
.header {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    max-height: 60rem;
    overflow: hidden;    
}

