/** Media Queries **/
@mixin telefono {
    @media (min-width: #{$telefono}) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: #{$tablet}) {
        @content;
    }
}
  
@mixin desktop {
    @media (min-width: #{$desktop}) {
        @content;
    }
}
@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin boton($bgColor, $display) {
    background-color: $bgColor;
    color: $blanco;
    font-weight: bold;
    text-decoration: none;
    padding: 1rem 3rem ;
    text-align: center;
    margin-top: calc($separacion / 2);
    display: $display;    
    text-transform: uppercase;
    border: none;
    border-radius: .3rem;

    &:hover {
        background-color: darken($bgColor, 5);
        cursor: pointer;
    }
}

@mixin headingBorder( $colorTexto, $colorLinea ) {
    font-size: 3.6rem;
    font-weight: 900;
    display: inline-block;
    margin-top: 1rem;
    color: $colorTexto;  
    font-weight: 900;  
    @include tablet { 
        margin-top: 0;
    }    
    &::after {
        content: '';
        background-color: $colorLinea;
        height: 1rem;
        width: calc( 100% + 1rem);
        display: block;
        margin: -2.4rem 0 0 0;
    }
}