.nav {
    display: flex;        
    margin: 4rem 0 0 0;         
    flex-direction: column;
    align-items: center;
    @include tablet {
        flex-direction: row;
        justify-content: flex-end; 
        
    }

    & a {            
        margin: 0 2rem;
        color: $blanco;
        padding-bottom: .7rem;
        display: inline-block;            
        border-bottom: .2rem solid transparent;
        font-size: 1.8rem;
        transition-property: color transform;
        transition-duration: .3s;
        font-weight: 300;            
        &:hover{                                 
            color: $gris;
            border-bottom: .2rem solid $gris;
            transform: translateY(.1rem);                         
        }             
    }
}