

.contacto {
    text-align: center;
    padding: 0; 
    @include tablet {
        padding: 5rem 0; 
    }
    &__titulo {
        @include headingBorder($secundario, $gris);
        margin: 4rem auto 6rem auto;           
    }
    &__formulario {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        & input[type="text"],
          input[type="email"],
          textarea {
            border: .3rem solid #cecece;
            border-radius: .3rem;
            padding: 1.5rem 2rem;
            font-size: 2rem;
            color: darken($gris, 20%);
        }
        & input[type="text"]:focus,
          input[type="email"]:focus,
          textarea:focus {
            border: .3rem solid $secundario;
        }
        & input[type="text"],
            input[type="email"] {
            width: 49%;
            margin-bottom: 1.5rem;
        }
        & textarea {
            width: 100%;
            height: 10rem;
            min-width: 100%;
            max-width: 100%;
            min-height: 10rem;
            max-height: 30rem;
            margin-bottom: 1.5rem;
        }
    }
    &__boton {
        @include boton($secundario, $blanco);
            margin: 2rem auto 6rem;              
        @include tablet {
            margin: 2rem auto;            
        }
    }

}