body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  font-size: 2rem;
}

a {
  text-decoration: none;
}

img,
picture {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 700;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

[class*=contenedor] {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.boton-primario {
  background-color: #1e345d;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  text-transform: uppercase;
  border: none;
  border-radius: 0.3rem;
}
.boton-primario:hover {
  background-color: #18294a;
  cursor: pointer;
}

.boton-secundario-block {
  background-color: #24539a;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  text-transform: uppercase;
  border: none;
  border-radius: 0.3rem;
}
.boton-secundario-block:hover {
  background-color: #1f4885;
  cursor: pointer;
}

.boton-secundario {
  background-color: #24539a;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  text-transform: uppercase;
  border: none;
  border-radius: 0.3rem;
}
.boton-secundario:hover {
  background-color: #1f4885;
  cursor: pointer;
}

.cursor-not-allowed {
  cursor: not-allowed;
}

.opacity-50 {
  opacity: 0.5;
}

.error-msj {
  border: 0.3rem solid #ff0033 !important;
  border-radius: 0.3rem;
  padding: 1.5rem 2rem;
}

.ok-msj {
  border: 0.3rem solid #119200 !important;
  border-radius: 0.3rem;
  padding: 1.5rem 2rem;
}

.error-bg {
  border: 0.3rem solid #ff0033;
  background-color: #FFCCCC;
  padding: 1.5rem 2rem;
  color: #ff0033;
  border-radius: 0.3rem;
  width: 100%;
  text-align: center;
  font-weight: 700;
  font-size: 1.7rem;
}

.msj {
  text-align: center;
  padding: 1.5rem 2rem;
  background-color: #119200;
  color: #FFFFFF;
  text-transform: uppercase;
  font-weight: 700;
  border-radius: 0.3rem;
  width: 100%;
  font-size: 1.7rem;
}

.webp .header {
  background-image: url("../img/bg.webp");
}

.avif .header {
  background-image: url("../img/bg.avif");
}

.notavif.notwebp .header {
  background-image: url("../img/bg.jpg") #e1e1e1;
}

.header {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  max-height: 60rem;
  overflow: hidden;
}

.nav {
  display: flex;
  margin: 4rem 0 0 0;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .nav {
    flex-direction: row;
    justify-content: flex-end;
  }
}
.nav a {
  margin: 0 2rem;
  color: #FFFFFF;
  padding-bottom: 0.7rem;
  display: inline-block;
  border-bottom: 0.2rem solid transparent;
  font-size: 1.8rem;
  transition-property: color transform;
  transition-duration: 0.3s;
  font-weight: 300;
}
.nav a:hover {
  color: #e1e1e1;
  border-bottom: 0.2rem solid #e1e1e1;
  transform: translateY(0.1rem);
}

.contenedor__texto {
  display: flex;
  justify-content: center;
  align-content: center;
  margin: 0 auto 15rem auto;
}
@media (min-width: 768px) {
  .contenedor__texto {
    margin: 5rem auto 15rem auto;
  }
}

.texto {
  display: inline-block;
  text-align: center;
  padding: 1rem;
}
.texto h1,
.texto h2 {
  color: #FFFFFF;
  font-weight: 300;
  margin: 0;
}
.texto__nombre {
  margin-bottom: 1rem;
  font-size: 3.5rem;
  line-height: 5rem;
  display: flex;
  overflow: hidden;
}
@media (min-width: 768px) {
  .texto__nombre {
    font-size: 5rem;
  }
}
.texto__nombre > div {
  display: flex;
  flex-direction: column;
  position: relative;
  transition-property: transform;
  transition-duration: 0.3s;
}
.texto__nombre > div.animacion {
  transform: translateY(-10rem);
}
.texto__nombre > div span {
  font-weight: 400;
}
.texto__nombre > div .segunda-linea {
  position: absolute;
  top: 10rem;
}
.texto__profesion {
  font-size: 1.5rem;
  background-color: #5a5a5a;
  border-radius: 0.3rem;
  padding: 0.3rem 2rem;
  transition-property: background-color box-shadow transform;
  transition-duration: 0.3s;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .texto__profesion {
    font-size: 2.3rem;
  }
}
.texto__profesion:hover {
  background-color: #1e345d;
  box-shadow: 0px 10px 15px 0px rgba(0, 0, 0, 0.75);
  transform: scale(1.01);
}

.acerca-de {
  text-align: center;
}
.acerca-de__contenedor {
  overflow: visible;
}
.acerca-de__foto {
  margin-top: -10rem;
}
.acerca-de__foto img {
  display: inline-block;
  border-radius: 50%;
  border: 0.5rem solid rgba(255, 255, 255, 0.2);
}
.acerca-de__texto {
  font-weight: 400;
  font-size: 2rem;
  width: 90%;
  margin: 4rem auto 4rem auto;
}
.acerca-de__texto-titulo {
  font-size: 3.6rem;
  font-weight: 900;
  display: inline-block;
  margin-top: 1rem;
  color: #24539a;
  font-weight: 900;
}
@media (min-width: 768px) {
  .acerca-de__texto-titulo {
    margin-top: 0;
  }
}
.acerca-de__texto-titulo::after {
  content: "";
  background-color: #e1e1e1;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -2.4rem 0 0 0;
}
.acerca-de__texto .bold {
  color: #1e345d;
  text-decoration: underline;
  font-weight: 700;
}
.acerca-de__btn {
  margin: 2rem auto 5rem auto;
}
.acerca-de__btn a {
  color: #FFFFFF;
}

.portafolios {
  background-color: #e1e1e1;
  padding: 5rem 0;
  text-align: center;
}
.portafolios__titulo {
  font-size: 3.6rem;
  font-weight: 900;
  display: inline-block;
  margin-top: 1rem;
  color: #24539a;
  font-weight: 900;
  margin: 4rem auto 6rem auto;
}
@media (min-width: 768px) {
  .portafolios__titulo {
    margin-top: 0;
  }
}
.portafolios__titulo::after {
  content: "";
  background-color: #FFFFFF;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -2.4rem 0 0 0;
}
.portafolios__grid {
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .portafolios__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .portafolios__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.portafolio {
  border: 1px solid #c8c8c8;
  border-radius: 0.3rem;
  background-color: #FFFFFF;
  overflow: hidden;
  max-width: 100%;
  transition-property: box-shadow transform;
  transition-duration: 0.3s;
}
.portafolio:hover {
  box-shadow: 0px 10px 15px 0px rgba(0, 0, 0, 0.75);
  transform: scale(1.01);
}
.portafolio__meta {
  background-color: #1e345d;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 0.4rem 0;
  color: #FFFFFF;
}
.portafolio__skills {
  font-size: 1.6rem;
}
.portafolio__nombre {
  font-size: 2.6rem;
  color: #24539a;
  margin: 0;
}
.portafolio__contenido {
  padding: 1rem;
}
.portafolio__descripcion {
  color: #5a5a5a;
  font-size: 1.6rem;
}
.portafolio__icono-github {
  margin: 0 auto;
}
.portafolio__enlace {
  background-color: #1e345d;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: #FFFFFF;
  text-transform: uppercase;
  border: none;
  border-radius: 0.3rem;
  display: block;
  font-size: 1.6rem;
  padding: 1rem;
}
.portafolio__enlace:hover {
  background-color: #18294a;
  cursor: pointer;
}
.portafolio__btn {
  margin: 5rem auto 2rem auto;
}
.portafolio__btn a {
  color: #FFFFFF;
}

.contacto {
  text-align: center;
  padding: 0;
}
@media (min-width: 768px) {
  .contacto {
    padding: 5rem 0;
  }
}
.contacto__titulo {
  font-size: 3.6rem;
  font-weight: 900;
  display: inline-block;
  margin-top: 1rem;
  color: #24539a;
  font-weight: 900;
  margin: 4rem auto 6rem auto;
}
@media (min-width: 768px) {
  .contacto__titulo {
    margin-top: 0;
  }
}
.contacto__titulo::after {
  content: "";
  background-color: #e1e1e1;
  height: 1rem;
  width: calc(100% + 1rem);
  display: block;
  margin: -2.4rem 0 0 0;
}
.contacto__formulario {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.contacto__formulario input[type=text],
.contacto__formulario input[type=email],
.contacto__formulario textarea {
  border: 0.3rem solid #cecece;
  border-radius: 0.3rem;
  padding: 1.5rem 2rem;
  font-size: 2rem;
  color: #aeaeae;
}
.contacto__formulario input[type=text]:focus,
.contacto__formulario input[type=email]:focus,
.contacto__formulario textarea:focus {
  border: 0.3rem solid #24539a;
}
.contacto__formulario input[type=text],
.contacto__formulario input[type=email] {
  width: 49%;
  margin-bottom: 1.5rem;
}
.contacto__formulario textarea {
  width: 100%;
  height: 10rem;
  min-width: 100%;
  max-width: 100%;
  min-height: 10rem;
  max-height: 30rem;
  margin-bottom: 1.5rem;
}
.contacto__boton {
  background-color: #24539a;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: #FFFFFF;
  text-transform: uppercase;
  border: none;
  border-radius: 0.3rem;
  margin: 2rem auto 6rem;
}
.contacto__boton:hover {
  background-color: #1f4885;
  cursor: pointer;
}
@media (min-width: 768px) {
  .contacto__boton {
    margin: 2rem auto;
  }
}

