.acerca-de {      
    text-align: center;
    
    &__contenedor {
        overflow: visible;
    } 
    &__foto {
        margin-top: -10rem;
        & img {
            display: inline-block;
            border-radius: 50%;
            border: .5rem solid rgba(255, 255, 255, 0.2);
        }
    }
    &__texto {            
        font-weight: 400;
        font-size: 2rem;        
        width: 90%;
        margin: 4rem auto 4rem auto;
        &-titulo {
            @include headingBorder( $secundario, $gris );
        }
        & .bold {
            color: $primario;
            text-decoration: underline;
            font-weight: 700;
        }
    }
    &__btn {
        margin: 2rem auto 5rem auto ;
        & a {
            color: $blanco;
        }
    }
    
} 
