.redes-sociales {
    background: $primario;
    padding: 4rem 0;
    &__contenedor {
        display: flex;
        flex-wrap: wrap;        
        justify-content: center;
        @include tablet {
            justify-content: center;            
        }
    }
    &__enlace {
        color: $blanco;
        text-align: center;
        width: 10rem;               
        display: block;
        padding: 1.5rem 0;
        border-radius: .3rem;
        font-size: 3rem;
        margin: 0 2rem;
        transition-property: background-color box-shadow transform ;
        transition-duration: .3s;
    }
    &__linkedin:hover {        
        background-color: $linkedin; 
        box-shadow: 0px 10px 15px 0px rgba(0,0,0,0.75); 
        transform: scale(1.01);       
    }
    &__github:hover {
        background-color: $github;
        box-shadow: 0px 10px 15px 0px rgba(0,0,0,0.75); 
        transform: scale(1.01);
    }
    &__twitter:hover {
        background-color: $twitter;
        box-shadow: 0px 10px 15px 0px rgba(0,0,0,0.75); 
        transform: scale(1.01);
    }
    &__facebook:hover {
        background-color: $facebook;
        box-shadow: 0px 10px 15px 0px rgba(0,0,0,0.75); 
        transform: scale(1.01);
    }
    &__youtube:hover {
        background-color: $youTube;
        box-shadow: 0px 10px 15px 0px rgba(0,0,0,0.75); 
        transform: scale(1.01);
    }
    &__instagram:hover {
        background-color: $instagram;
        box-shadow: 0px 10px 15px 0px rgba(0,0,0,0.75); 
        transform: scale(1.01);
    }
  
}